import Vue from "vue";
import Vuex from "vuex";
import router from "../router";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  //    state
  state: {
    //   url
    imgURL: "http://localhost:80/upload/",
    //    
    userInfo: JSON.parse(sessionStorage.getItem("user")),
    //     
    allUsers: []
  },
  getters: {
    // getters  , 
    allUsersCount: state => {
      return state.allUsers.length;
    },
    countOfPosition: state => {
      let count = 0;
      state.allUsers.forEach(user => {
        if (user.position === "") count++;
      });
      return count;
    },
    percentOfDeveloper: (state, getters) => {
      return Math.round(
        (getters.countOfPosition / getters.allUsersCount) * 100
      );
    }
  },
  // state  
  // mutation (state, {data1, data2}) { }
  mutations: {
    //  
    login(state, payload) {
      if (payload !== null) {
        sessionStorage.setItem("user", JSON.stringify(payload));
        state.userInfo = JSON.parse(sessionStorage.getItem("user"));
      }
    },
    // 
    logout(state, payload) {
      if (payload !== null) {
        sessionStorage.removeItem("user");
        state.userInfo = null;
      }
    },
    userInfoChange(state, payload) {
      if (payload !== null) {
        sessionStorage.setItem("user", JSON.stringify(payload));
        alert(state.userInfo);
      }
    }
  },

  //    
  // actions  state    mutations  commit  
  /*
  action  context   . 
  context  store     . 
   context.commit  Mutation  , 
  context.state context.getters  state getter    .
  mutations  : commit('', 'parameter')
  actions  : dispatch('', 'parameter')
  */
  actions: {
    loginActions({ commit }, loginObj) {
      axios
        .post(
          "http://localhost:80/login?userid=" +
            loginObj.userid +
            "&password=" +
            loginObj.password
        )
        .then(response => {
          commit("login", response.data);
          router.push({ name: "mypage" });
        })
        .catch(e => {
          alert("ID Password .");
          console.log(e);
        });
    },
    logoutActions({ commit }, loginObj) {
      commit("logout", loginObj);
      router.push({ name: "Home" });
    }
  }
});
