import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Login from "../views/loginUserView/Login.vue";
import MyPage from "../views/loginUserView/MyPage.vue";
import InsertUser from "../views/loginUserView/InsertUser.vue";
import UpdateUser from "../views/loginUserView/UpdateUser.vue";
import MyPortfolio from "../views/portfolioView/MyPortfolio.vue";
import PortfolioList from "../views/portfolioView/PortfolioList.vue";
import InsertPortfolio from "../views/portfolioView/InsertPortfolio.vue";
import UpdatePortfolio from "../views/portfolioView/UpdatePortfolio.vue";

Vue.use(VueRouter);
//   reject
const rejectAuthUser = (to, from, next) => {
  if (sessionStorage.getItem("user") != null) {
    //      
    alert("  .");
    //  redirect
    next("/");
  } else {
    next();
  }
};

// if (store.state.isLogin === false) {
const onlyAuthUser = (to, from, next) => {
  if (sessionStorage.getItem("user") == null) {
    //       
    alert(" .");
    //  redirect
    next("/");
  } else {
    next();
  }
};
const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/login",
    name: "Login",
    //     
    beforeEnter: rejectAuthUser,
    component: Login
  },
  {
    path: "/mypage",
    name: "mypage",
    beforeEnter: onlyAuthUser,
    component: MyPage,
    children: [
      {
        path: "update",
        name: "updateUser",
        component: UpdateUser
      }
    ]
  },
  {
    path: "/insertuser",
    name: "insertuser",
    component: InsertUser
  },
  //  
  {
    path: "/portfolio",
    // alias: "/portfolio",
    name: "myportfolio",
    component: MyPortfolio
  },
  {
    path: "/portfolios",
    // alias: "/portfolios",
    name: "portfoliolist",
    component: PortfolioList
  },
  {
    path: "/insertportfolio",
    name: "insertportfolio",
    component: InsertPortfolio
  },
  {
    path: "/updateportfolio",
    name: "updatePortfolio",
    component: UpdatePortfolio
  },
  //  
  {
    path: "/profile",
    name: "Profile",
    component: () => import("../views/Profile.vue")
  },
  {
    path: "/project",
    name: "Project",
    component: () => import("../views/Project.vue")
  },
  {
    path: "/insertproject",
    name: "insertproject",
    component: () => import("../views/InsertProject.vue")
  },
  {
    path: "/project/:pjSeq/details",
    name: "details",
    component: () => import("../components/ProjectDetails.vue"),
    props: true
  },
  {
    path: "/mentor",
    name: "Mentor",
    component: () => import("../views/Mentor.vue")
  },
  {
    path: "/signup",
    name: "SignUp",
    component: () => import("../views/SignUp.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
