<template>
  <section>
    <div class="container is-max-desktop">
      <div class="notification is-accent">
        <b-field label="ID" type="" message="" align="left">
          <b-input
            v-model="userid"
            placeholder=""
            maxlength="30"
            size="is-medium"
          ></b-input>
        </b-field>

        <b-field label="Password" type="" message="" align="left">
          <b-input
            v-model="password"
            type="password"
            placeholder=""
            maxlength="30"
            size="is-medium"
          >
          </b-input>
        </b-field>
        <b-button
          type="is-primary"
          outlined
          @click="
            loginActions({
              userid: userid,
              password: password
            })
          "
          position="is-centered"
          size="is-large"
          ></b-button
        >
        <br /><br />
      </div>
    </div>
  </section>
</template>
<script>
/*
helper  

mapState - state  

mapGetters - getters  

mapMutations - mutations  

mapActions - actions  
*/
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      userid: null,
      password: null
    };
  },
  computed: {
    ...mapState([])
  },
  methods: {
    // store action login   
    ...mapActions(["loginActions"])
  }
};
</script>
